.footer{
    background-color: #fff5f6;
    padding-top: 57px;
    padding-bottom: 25px;
    @include tablet{
        padding-top: 79px;
        padding-bottom: 18px;
    }
    @include desktop{
        padding-top: 83px;
        padding-bottom: 10px;
    }
    &__container{
        display: flex;
        flex-direction: column;
        margin-bottom: 43px;
        @include tablet{
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 91px;
        }
        @include desktop{
            margin-bottom: 97px;
        }
    }
    &__title-cont{
        @media screen and (max-width: 767px){
            max-width: 230px;
            margin-bottom: 43px;
        }
        @media (min-width: 768px) and (max-width: 1279px) {
            max-width: 340px;
        }
    }   
    &__title{
        text-align: left; 
    }
    &__pretitle{
        text-align: left;
        margin-bottom: 17px;
    }
    &__social-cont{
        align-items: flex-end;
    }
    &__contacts{
        font-weight: bold;
        font-size: 12px;
        line-height: 1.86;
        text-align: right;
        @include tablet{
            font-size: 14px;
        }
        @include desktop{
            font-size: 16px;
        }
    }
    &__contacts-link{
        color: var(--accent-color);
    }
}





.social-list{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 46px;
    @include desktop{
        margin-bottom: 50px;
    }
    &__link{
        @include centerFlex;
         width: 32px;
         height: 32px;
         border-radius: 50%;
         background-color: var(--icon-bg-color);
         color: var(--text-white-color);
         &:focus, &:hover{
             background-color: var(--accent-color);
         }
         @include tablet{
             width: 34px;
             height: 34px;
         }
    }
     &__icon{
        fill: currentColor;
    }
    &__item + &__item{
        margin-left: 28px;
        @include tablet{
            margin-left: 30px;
        }
        @include desktop{
            margin-left: 24px;
        }
        
    }
    
  
}


.footer-bottom{
    border-top:  1px solid #E1E1E1;
    padding-top: 22px;
    @include tablet{
        padding-top: 29px;
    }
    @include desktop{
        padding-top: 9px;
    }
    &__text{
        font-weight: bold;
        line-height: 1.52;
        letter-spacing: 0.02em;
        text-align: center;
        color: var(--text-grey-color);
        @media screen and (max-width: 479px) {
            width: 150px;
            margin: 0 auto;
            }
        
        @include tablet{
            line-height: 1.86;
        }
    }
  
}